import React from "react";
import { graphql } from 'gatsby';
import { getImage } from "gatsby-plugin-image";
import { FaChevronLeft } from 'react-icons/fa';

import Page from "../../components/layout/page";
import PageHead from '../../components/base/head-base';
import { LayoutContainer, LayoutItem, LayoutItemWrapper, LayoutRow } from "../../components/layout/layout";
import IconLink from "../../components/base/links/icon-link";
import ButtonLink from "../../components/base/links/button-link";
import { Heading } from "../../components/base/typography";
import ContentWrapper from "../../components/markdown/content-wrapper";
import Tag from "../../components/base/tag";
import Swiper from "../../components/swiper/swiper";

const mapImages = (data) => {
  const mappedImageStructure = data.strapiWork.workphotos.map((item) => {
    const id = item.id
    const image = getImage(item.localFile.childImageSharp);
    const description = item.alternativeText;

    return {
      id: id,
      image: image,
      description: description
    }
  });

  return mappedImageStructure;
}

export default function WorkDetail({ data }) {
  const mappedImages = mapImages(data);

  return (
    <Page
      active="work"
    >
      <LayoutContainer>
        <LayoutRow>
          <LayoutItemWrapper center>
            <LayoutItem
              center
              fullWidth
              maxWidth={'40rem'}
            >
              <Swiper images={mappedImages} />
            </LayoutItem>
          </LayoutItemWrapper>
          <LayoutItemWrapper rowGap columnCenter>
            <div>
              <IconLink to="/work">
                <FaChevronLeft />
                Back to overview
              </IconLink>
            </div>
            <div>
              <Heading level={2}>{data.strapiWork.name}</Heading>
              <Tag>{data.strapiWork.category.name}</Tag>
            </div>
            <ContentWrapper
              htmlAst={data.strapiWork.description.data.childMarkdownRemark.htmlAst}
              options={{
                p: {
                  topMargin: false
                }
              }}
            />
            <div>
              <ButtonLink href={data.strapiWork.demoUrl} as="a">View demo</ButtonLink>
            </div>
          </LayoutItemWrapper>
        </LayoutRow>
      </LayoutContainer>
    </Page>
  )
};

export const Head = ({ location, data }) => {
  return (
    <PageHead
      pathname={location.pathname}
      title={data.strapiWork.name}
      description={data.strapiWork.description.data.childMarkdownRemark.excerpt}
    // keyword={data.strapiPost.SEO.keywords}
    // image={data.strapiPost.cover}
    />
  )
};

export const query = graphql`
query getWorkItem($slug: String) {
  strapiWork(slug: {eq: $slug}) {
    id
    name
    demoUrl
    category {
      name
    }
    workphotos {
      id
      localFile {
        childImageSharp {
          gatsbyImageData
        }
      }
      alternativeText
    }
    description {
      data {
        childMarkdownRemark {
          htmlAst
          excerpt
        }
      }
    }
  }
}
`